 
 .logo{
     padding: 16px 0px;
     
     height: 30px;
     
 }
 .navbar ul{
    display: flex;
    list-style: none;
    margin-right: 1px;
}
.navbar{
    padding: 0px 36.594px;
    background-color: #000000;
    height: 65px;

}
.navbar .navend{
    padding: 10px 0px;
    font-size: 12px;
    color: white;
    display: flex;
    justify-content: flex-end;
    align-items:center;
    line-height: 50px;
    margin: 0px 8px;
    background-color:transparent;
     
    float: right;
    margin-right: 10px;
    
}
.jbtn{
    padding: 10px;
    background: #e20712;
    border: none;
    border-radius: 3px;
    color: white;
    margin: 0px 10px;
    width: 105px;
    cursor: pointer;
}
.sbtn{
    padding: 12px;
    background: #181818;
    border: .1px solid white;
    border-radius: 2px;
    color: white;
    margin: 0px 5px;
    width: 98px;
    height: 35px;
    cursor: pointer;
    text-align: center;
    text-transform: uppercase;



}
@media(max-width:768px){
    .sbtn{
        display: none;
    }
    .navend p{
        display: none;
    }
    .navbar{
        padding: 0px 25px;
        background-color: #000000;
        height: 65px;
    
    }
    .jbtn {
        padding: 10px;
        background: #e20712;
        border: none;
        border-radius: 3px;
        color: white;
        margin: 4px 0px;
        width: 105px;
        cursor: pointer;
    }
}
@media (max-width:375px){
    .navbar{
        padding: 0px 15px;
        background-color: #000000;
        height: 60px;
    
    }
    .jbtn{
        padding: 8px;
        background: #e20712;
        border: none;
        border-radius: 3px;
        color: white;
        margin: 4px 0px;
        width: 87px;
        cursor: pointer;
    }
}